import React from "react";
import { Box, Typography, Divider, Link, makeStyles } from "@material-ui/core";
import theme from "../../theme/thems";
import * as styles from "../../theme/commonStyles";

import resumePdf from "../../documents/Vinay_Rinait_Resume.pdf";
import my from "./../../images/my.jpg";

import SkillsPage from "./SkillsPage.js";
import ToolsPage from "./ToolsPage";

const AboutPage = () => {
  const useStyles = makeStyles({
    columnDiv: {
      ...styles.columnDiv,
    },
    mainDiv: {
      [theme.breakpoints.down("xl")]: {
        width: "45%",
      },
      [theme.breakpoints.down("lg")]: {
        width: "50%",
      },
      [theme.breakpoints.down("md")]: {
        width: "60%",
      },
      [theme.breakpoints.down("sm")]: {
        width: "80%",
      },
      [theme.breakpoints.down("xs")]: {
        width: "90%",
      },
      margin: "0 auto",
    },
    headerText: {
      ...styles.headerText,
    },
    aboutText: {
      marginTop: "6rem",
    },
    divider: {
      ...styles.divider,
    },
    bodyText: {
      fontSize: "1.1rem",
      color: "black",
      maxWidth: "100%",
      maxHeight: "100%",
      margin: "1rem",
      marginBottom: "0.02rem",
      lineHeight: "1.6rem",
      [theme.breakpoints.down("xs")]: {
        fontSize: "0.9rem",
      },
    },
    link: {
      color: "black",
      textShadow: "1px 1px #b2dfdb",
      textDecoration: "none",
      borderBottom: "1px solid #b2dfdb",
      "&:hover": {
        color: "#b2dfdb",
        cursor: "pointer",
      },
    },
    picture: {
      maxHeight: "100%",
      maxWidth: "100%",
      margin: "2rem",
      marginBottom: "6rem",
      width: "40%",
      [theme.breakpoints.down("md")]: {
        width: "50%",
      },
      [theme.breakpoints.down("sm")]: {
        width: "60%",
      },
      [theme.breakpoints.down("xs")]: {
        width: "70%",
      },
      boxShadow: "8px 8px 12px rgba(0, 0, 0, 0.25)",
    },
  });
  const classes = useStyles();

  return (
    <div style={{ backgroundColor: "#fcf8f8" }}>
      <Box className={`${classes.columnDiv} ${classes.mainDiv}`} id="AboutMe">
        <Typography className={`${classes.aboutText} ${classes.headerText}`}>
          About Me
        </Typography>
        <Divider classes={{ root: classes.divider }} />
        <Typography className={classes.bodyText}>
          My name is <b>Vinay Rinait</b>
        </Typography>
        <Typography className={classes.bodyText}>
          {" "}
          Mern Stack Developer. Expert in creating user-priotized UI and optimum
          Frontend. Also have excellence in Backend with Node, express and
          MongoDB. Problem solving mindset, hustler and lifelong learner. Good
          with communication and interested in work collaborative projects.
        </Typography>

        <Typography className={classes.bodyText}>
          <Link
            href={resumePdf}
            target="_blank"
            className={classes.link}
            underline="none"
          >
            Click here to view my full resume.
          </Link>
        </Typography>
        <Box
          component="img"
          src={my}
          alt="A picture of me"
          className={classes.picture}
        />
      </Box>
      <SkillsPage />
      <ToolsPage />
    </div>
  );
};

export default AboutPage;
