import React, { useState } from "react";
import {
  Box,
  Typography,
  Divider,
  Button,
  makeStyles,
} from "@material-ui/core";
import * as styles from "../../theme/commonStyles";

import Project from "./../Project";

import netmeds from "../../images/netmeds.png.png";
import gym from "../../images/gym.png";
import ss from "../../images/ss.png";
// my-portfolio\src\images\netmeds.png.png
import GitHubCalendar from "react-github-calendar";
const GithubPage = () => {
  const useStyles = makeStyles({
    columnDiv: {
      ...styles.columnDiv,
      width: "auto",
      paddingBottom: "5rem",
    },
    headerText: {
      ...styles.headerText,
      //   marginTop: "1rem",
    },
    divider: {
      ...styles.divider,
      paddingBottom: "0.15rem",
    },
    icons: {
      fontSize: "1.5rem",
      display: "flex",
      width: "100%",
      margin: "0.5rem 0",
      gap: "0.7rem",
    },
    button: {
      margin: "0.5rem",
      marginTop: "1rem",
      backgroundColor: "#347f78",
      color: "white",
      boxShadow: "1px 1px 2px rgba(0, 0, 0, 0.3)",
      "&:hover": {
        backgroundColor: "#409b93",
      },
    },
  });
  const classes = useStyles();

  const [showMore, setShowMore] = useState(false);

  const reactIcon = (
    <span
      className="iconify"
      data-icon="logos:react"
      data-inline="false"
      style={{ fontSize: "1.5rem" }}
    />
  );

  const reduxIcon = (
    <span
      className="iconify"
      data-icon="logos:redux"
      data-inline="false"
      style={{ fontSize: "1.5rem" }}
    />
  );

  const javascriptIcon = (
    <span
      className="iconify"
      data-icon="logos:javascript"
      data-inline="false"
      style={{ fontSize: "1.6rem" }}
    />
  );

  const htmlIcon = (
    <span
      className="iconify"
      data-icon="vscode-icons:file-type-html"
      data-inline="false"
      style={{ fontSize: "1.7rem" }}
    />
  );

  const cssIcon = (
    <span
      className="iconify"
      data-icon="vscode-icons:file-type-css"
      data-inline="false"
      style={{ fontSize: "1.7rem" }}
    />
  );

  const javaIcon = (
    <span
      className="iconify"
      data-icon="logos:java"
      data-inline="false"
      style={{ fontSize: "1.7rem" }}
    />
  );

  const pythonIcon = (
    <span
      className="iconify"
      data-icon="logos:python"
      data-inline="false"
      style={{ fontSize: "1.7rem" }}
    ></span>
  );

  const nodeIcon = (
    <img
      src="https://cdn.jsdelivr.net/gh/devicons/devicon/icons/nodejs/nodejs-original.svg"
      alt="Nodejs Icon"
      style={{
        width: "1.7rem",
        height: "1.7rem",
      }}
    />
  );

  const mongodbIcon = (
    <img
      src="https://cdn.jsdelivr.net/gh/devicons/devicon/icons/mongodb/mongodb-original.svg"
      alt="MongoDB Icon"
      style={{
        width: "1.7rem",
        height: "1.7rem",
      }}
    />
  );

  const cPlusPlusIcon = (
    <img
      src="https://cdn.jsdelivr.net/gh/devicons/devicon/icons/cplusplus/cplusplus-original.svg"
      alt="C++ Icon"
      style={{
        width: "1.7rem",
        height: "1.7rem",
      }}
    />
  );

  const gdmcIcon = <Box className={classes.icons}>{pythonIcon}</Box>;

  const toDoListIcons = (
    <Box className={classes.icons}>
      {reactIcon}
      {reduxIcon}
      {javascriptIcon}
      {htmlIcon}
      {cssIcon}
    </Box>
  );

  const babyLizardIcons = <Box className={classes.icons}>{javaIcon}</Box>;

  const calculatorIcons = (
    <Box className={classes.icons}>
      {reactIcon}
      {javascriptIcon}
      {htmlIcon}
      {cssIcon}
    </Box>
  );

  const cssArtIcons = (
    <Box className={classes.icons}>
      <span
        className="iconify"
        data-icon="vscode-icons:file-type-html"
        data-inline="false"
        style={{ fontSize: "1.7rem" }}
      />
      {cssIcon}
    </Box>
  );

  const academicProjectsIcons = (
    <Box className={classes.icons}>
      {javaIcon}
      {javascriptIcon}
      {cPlusPlusIcon}
    </Box>
  );

  const covidTrackerIcons = (
    <Box className={classes.icons}>
      {reactIcon}
      {nodeIcon}
      {mongodbIcon}
      {javascriptIcon}
      {htmlIcon}
      {cssIcon}
    </Box>
  );

  return (
    <Box
      className={classes.columnDiv}
      id="Projects"
      style={{ backgroundColor: "#fcf8f8" }}
    >
      <Typography className={classes.headerText}>  GitHub Stats:</Typography>
      <Divider className={classes.divider} display={"flex"} />
      <Project
        image={
          "https://github-readme-stats.vercel.app/api?username=VinayRinait&theme=dark&hide_border=false&include_all_commits=true&count_private=true"
        }
        link1="https://github.com/VinayRinait"
      />
      <Project
        image={
          "https://github-readme-streak-stats.herokuapp.com/?user=VinayRinait&theme=dark&hide_border=false"
        }
        link1="https://github.com/VinayRinait"
      />
      <Project
        image={
          "https://github-profile-summary-cards.vercel.app/api/cards/profile-details?username=VinayRinait&theme=dracula"
        }
        link1="https://github.com/VinayRinait"
      />
      <GitHubCalendar username="VinayRinait" />
    </Box>
  );
};

export default GithubPage;
