import React from "react";
import {
  Box,
  Typography,
  Divider,
  useMediaQuery,
  makeStyles,
} from "@material-ui/core";
import theme from "../../theme/thems";
import * as styles from "../../theme/commonStyles";
import sqliteLogo from "./../../images/sqliteLogo.png";

import SkillBox from "../SkillBox";

const ToolsPage = () => {
  const useStyles = makeStyles({
    columnDiv: {
      ...styles.columnDiv,
    },
    mainDiv: {
      backgroundColor: "#e8e8e8",
      margin: "0 auto",
      paddingBottom: "2.25rem",
    },
    rowDiv: {
      ...styles.rowDiv,
    },
    rowDivider: {
      width: "70%",
      [theme.breakpoints.down("sm")]: {
        width: "95%",
      },
    },
    divider: {
      ...styles.divider,
    },
    headerText: {
      ...styles.headerText,
    },
    skillsText: {
      marginTop: "2rem",
    },
    listItemText: {
      color: "black",
      fontSize: "1.2rem",
      margin: "0 1rem",
    },
  });
  const classes = useStyles();
  const showSmallRows = useMediaQuery(theme.breakpoints.down("sm"));

  const python = (
    <SkillBox
      title="PYTHON"
      link="https://cdn.jsdelivr.net/gh/devicons/devicon/icons/python/python-original.svg"
    />
  );
  const javascript = (
    <SkillBox
      title="POSTMAN"
      link="https://res.cloudinary.com/dguq5ln6o/image/upload/v1684079511/getpostman-icon_vercz3.svg"
    />
  );
  const cplusplus = (
    <SkillBox
      title="C++"
      link="https://cdn.jsdelivr.net/gh/devicons/devicon/icons/cplusplus/cplusplus-original.svg"
    />
  );
  const java = (
    <SkillBox
      title="JAVA"
      link="https://cdn.jsdelivr.net/gh/devicons/devicon/icons/java/java-original.svg"
    />
  );
  const unity = (
    <SkillBox
      title="UNITY"
      link="https://cdn.jsdelivr.net/gh/devicons/devicon/icons/unity/unity-original.svg"
    />
  );
  const react = (
    <SkillBox
      title="BOOTSTRAP"
      link="https://cdn.jsdelivr.net/gh/devicons/devicon/icons/bootstrap/bootstrap-original.svg"
    />
  );
  const node = (
    <SkillBox
      title="AWS"
      link="https://cdn.jsdelivr.net/gh/devicons/devicon/icons/amazonwebservices/amazonwebservices-original.svg"
    />
  );
  const redux = (
    <SkillBox
      title="TAILWIND"
      link="https://cdn.jsdelivr.net/gh/devicons/devicon/icons/tailwindcss/tailwindcss-plain.svg"
    />
  );
  const html = (
    <SkillBox
      title="GITHUB"
      link="https://cdn.jsdelivr.net/gh/devicons/devicon/icons/github/github-original.svg"
    />
  );
  const css = (
    <SkillBox
      title="VSCODE"
      link="https://cdn.jsdelivr.net/gh/devicons/devicon/icons/vscode/vscode-original.svg"
    />
  );
  const git = (
    <SkillBox
      title="GIT"
      link="https://cdn.jsdelivr.net/gh/devicons/devicon/icons/git/git-original.svg"
    />
  );

  const sqlite = <SkillBox title="SQLITE" image={sqliteLogo} />;
  const figma = (
    <SkillBox
      title="EXPRESS "
      link="https://cdn.jsdelivr.net/gh/devicons/devicon/icons/express/express-original.svg"
    />
  );

  const normalRows = (
    <Box className={classes.rowDivider}>
      <Box className={classes.rowDiv} id="Skills"></Box>
      <Box className={classes.rowDiv}>
        {javascript}
        {react}
        {node}

        {html}
        {css}
      </Box>
      <Box className={classes.rowDiv}>
        {git}
        {redux}
        {/* {figma} */}
      </Box>
    </Box>
  );
  const smallRows = (
    <Box className={classes.rowDivider}>
      <Box className={classes.rowDiv}>{javascript}</Box>
      <Box className={classes.rowDiv}>{git}</Box>
      <Box className={classes.rowDiv}>
        {react}
        {node}
        {redux}
      </Box>
      <Box className={classes.rowDiv}>
        {html}
        {css}
        {/* {figma} */}
      </Box>
    </Box>
  );

  return (
    <Box id="Skills" className={`${classes.columnDiv} ${classes.mainDiv}`}>
      <Typography
        className={`${classes.skillsText} ${classes.headerText}`}
        align="center"
      >
        Tools
      </Typography>
      <Divider classes={{ root: classes.divider }} />
      {showSmallRows ? smallRows : normalRows}
    </Box>
  );
};

export default ToolsPage;
