import React from "react";
import {
  Box,
  Typography,
  Link,
  IconButton,
  Divider,
  Tooltip,
  makeStyles,
  Zoom,
} from "@material-ui/core";
import GitHubIcon from "@material-ui/icons/GitHub";
import LinkedInIcon from "@material-ui/icons/LinkedIn";

import theme from "../../theme/thems";
import * as styles from "../../theme/commonStyles";

const ContactPage = () => {
  const useStyles = makeStyles({
    mainDiv: {
      backgroundColor: "rgba(0, 0, 0, 0.92)",
      position: "relative",
      bottom: "0",
      paddingTop: "1rem",
      paddingBottom: "0.6rem",
    },
    columnDiv: {
      ...styles.columnDiv,
    },
    rowDiv: {
      ...styles.rowDiv,
    },
    headerText: {
      ...styles.headerText,
    },
    contactText: {
      color: "#eeeeee",
      margin: "0.5rem",
      textShadow: "0.1rem 0.1rem #4f4f4f",
    },
    divider: {
      ...styles.divider,
    },
    contactDivider: {
      backgroundColor: "#eeeeee",
    },
    emailText: {
      color: "#eeeeee",
      fontSize: "1.1rem",
      fontWeight: 500,
      [theme.breakpoints.down("sm")]: {
        fontSize: "0.5rem",
      },
    },
    mobileText: {
      color: "#eeeeee",
      fontSize: "1.1rem",
      fontWeight: 500,
      [theme.breakpoints.down("sm")]: {
        fontSize: "0.5rem",
      },
    },
    link: {
      fontSize: "1.1rem",
      fontWeight: 500,
      marginRight: "10px",
      [theme.breakpoints.down("sm")]: {
        fontSize: "0.5rem",
        display: "block",
        // marginBottom: "10px",
      },
    },
    icon: {
      color: "#eeeeee",
      [theme.breakpoints.down("xl")]: {
        margin: "0.2rem 1.4rem",
      },
      [theme.breakpoints.down("md")]: {
        margin: "0.2rem 0.5rem",
      },
      [theme.breakpoints.down("sm")]: {
        margin: "0.1rem",
      },
    },
    github: {
      fontSize: "2.2rem",
    },
    linkedIn: {
      fontSize: "2.7rem",
    },
    twitter: {
      fontSize: "2.6rem",
    },
  });

  const classes = useStyles();

  const icons = (
    <Box className={classes.rowDiv} id="Contact">
      <Tooltip title="LinkedIn" arrow TransitionComponent={Zoom}>
        <IconButton
          className={classes.icon}
          aria-label="LinkedIn Link"
          onClick={() =>
            window.open("https://www.linkedin.com/in/vinay-rinait-aa082b246/")
          }
        >
          <LinkedInIcon className={classes.linkedIn} />
        </IconButton>
      </Tooltip>
      <Tooltip title="GitHub" arrow TransitionComponent={Zoom}>
        <IconButton
          className={classes.icon}
          aria-label="GitHub link"
          onClick={() => window.open("https://github.com/VinayRinait")}
        >
          <GitHubIcon className={classes.github} />
        </IconButton>
      </Tooltip>
    </Box>
  );

  return (
    <Box className={`${classes.columnDiv} ${classes.mainDiv}`}>
      <Typography className={`${classes.headerText} ${classes.contactText}`}>
        Contact Me
      </Typography>
      <Divider
        classes={{
          root: `${classes.divider} ${classes.contactDivider}`,
        }}
      />
      <Box className={classes.rowDiv} gap="15px">
        <Typography className={classes.emailText}>Email:</Typography>
        <Link href="mailto: vinaymasai1998@gmail.com" className={classes.link}>
          vinaymasai1998@gmail.com
        </Link>

        <Typography className={classes.mobileText}>Mobile:</Typography>
        <Link href="#" className={classes.link}>
          7709500433
        </Link>
      </Box>
      {icons}
    </Box>
  );
};

export default ContactPage;
