import React from "react";
import {
  Box,
  Typography,
  Divider,
  useMediaQuery,
  makeStyles,
} from "@material-ui/core";
import theme from "../../theme/thems";
import * as styles from "../../theme/commonStyles";
import sqliteLogo from "./../../images/sqliteLogo.png";

import SkillBox from "../SkillBox";

const NewSkillsPage = () => {
  const useStyles = makeStyles({
    columnDiv: {
      ...styles.columnDiv,
    },
    mainDiv: {
      backgroundColor: "#e8e8e8",
      margin: "0 auto",
      paddingBottom: "2.25rem",
    },
    rowDiv: {
      ...styles.rowDiv,
    },
    rowDivider: {
      width: "70%",
      [theme.breakpoints.down("sm")]: {
        width: "95%",
      },
    },
    divider: {
      ...styles.divider,
    },
    headerText: {
      ...styles.headerText,
    },
    skillsText: {
      marginTop: "2rem",
    },
    listItemText: {
      color: "black",
      fontSize: "1.2rem",
      margin: "0 1rem",
    },
  });
  const classes = useStyles();
  const showSmallRows = useMediaQuery(theme.breakpoints.down("sm"));

  const python = (
    <SkillBox
      title="PYTHON"
      link="https://cdn.jsdelivr.net/gh/devicons/devicon/icons/python/python-original.svg"
    />
  );
  const javascript = (
    <SkillBox
      title="JAVASCRIPT"
      link="https://cdn.jsdelivr.net/gh/devicons/devicon/icons/javascript/javascript-original.svg"
    />
  );
  const cplusplus = (
    <SkillBox
      title="C++"
      link="https://cdn.jsdelivr.net/gh/devicons/devicon/icons/cplusplus/cplusplus-original.svg"
    />
  );
  const java = (
    <SkillBox
      title="JAVA"
      link="https://cdn.jsdelivr.net/gh/devicons/devicon/icons/java/java-original.svg"
    />
  );
  const unity = (
    <SkillBox
      title="UNITY"
      link="https://cdn.jsdelivr.net/gh/devicons/devicon/icons/unity/unity-original.svg"
    />
  );
  const react = (
    <SkillBox
      title="REACT JS"
      link="https://cdn.jsdelivr.net/gh/devicons/devicon/icons/react/react-original.svg"
    />
  );
  const node = (
    <SkillBox
      title="NODE JS"
      link="https://cdn.jsdelivr.net/gh/devicons/devicon/icons/nodejs/nodejs-original.svg"
    />
  );
  const redux = (
    <SkillBox
      title="REDUX"
      link="https://cdn.jsdelivr.net/gh/devicons/devicon/icons/redux/redux-original.svg"
    />
  );
  const html = (
    <SkillBox
      title="HTML"
      link="https://cdn.jsdelivr.net/gh/devicons/devicon/icons/html5/html5-original.svg"
    />
  );
  const css = (
    <SkillBox
      title="CSS"
      link="https://cdn.jsdelivr.net/gh/devicons/devicon/icons/css3/css3-original.svg"
    />
  );
  const git = (
    <SkillBox
      title="TYPESCRIPT"
      link="https://cdn.jsdelivr.net/gh/devicons/devicon/icons/typescript/typescript-original.svg"
    />
  );
  const nxtjs = (
    <SkillBox
      title="NEXT JS"
      link="https://cdn.jsdelivr.net/gh/devicons/devicon/icons/nextjs/nextjs-original.svg"
    />
  );
  const mongodb = (
    <SkillBox
      title="MONGODB"
      link="https://cdn.jsdelivr.net/gh/devicons/devicon/icons/mongodb/mongodb-original.svg"
    />
  );
  const sqlite = <SkillBox title="SQLITE" image={sqliteLogo} />;
  const figma = (
    <SkillBox
      title="EXPRESS "
      link="https://cdn.jsdelivr.net/gh/devicons/devicon/icons/express/express-original.svg"
    />
  );

  const normalRows = (
    <Box className={classes.rowDivider}>
      <Box className={classes.rowDiv} id="Skills"></Box>
      <Box className={classes.rowDiv}>
        {javascript}
        {react}
        {node}

        {html}
        {css}
      </Box>
      <Box className={classes.rowDiv}>
        {git}
        {redux}
        {figma}
        {mongodb}
        {nxtjs}
      </Box>
    </Box>
  );
  const smallRows = (
    <Box className={classes.rowDivider}>
      <Box className={classes.rowDiv}>{javascript}</Box>
      <Box className={classes.rowDiv}>{git}</Box>
      <Box className={classes.rowDiv}>
        {react}
        {node}
        {redux}
      </Box>
      <Box className={classes.rowDiv}>
        {html}
        {css}
        {figma}
      </Box>
      <Box className={classes.rowDiv}>
        {mongodb}
        {nxtjs}
      </Box>
    </Box>
  );

  return (
    <Box id="Skills" className={`${classes.columnDiv} ${classes.mainDiv}`}>
      <Typography
        className={`${classes.skillsText} ${classes.headerText}`}
        align="center"
      >
        Skills
      </Typography>
      <Divider classes={{ root: classes.divider }} />
      {showSmallRows ? smallRows : normalRows}
    </Box>
  );
};

export default NewSkillsPage;
